import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Home.css";

const works = [
    { id: 1, image: "/images/work1.png", description: "  -  " },
    { id: 2, image: "/images/work2.png", description: " ,    " },
    { id: 3, image: "/images/work3.png", description: "  " },
    { id: 4, image: "/images/work4.png", description: ",   " },
    { id: 5, image: "/images/work5.png", description: "    " },
    { id: 6, image: "/images/work6.png", description: "   -" },
    { id: 7, image: "/images/work7.png", description: "    " },
    { id: 8, image: "/images/work8.png", description: "     " },
    { id: 9, image: "/images/work9.png", description: "  " },
    { id: 10, image: "/images/work10.png", description: "  " },
    { id: 11, image: "/images/work11.png", description: "    " },
    { id: 12, image: "/images/work12.png", description: "   " },
    { id: 13, image: "/images/work13.png", description: "      " },
    { id: 14, image: "/images/work14.png", description: "-  " },
];

const Home = () => {
    const navigate = useNavigate();
    const [user, setUser] = useState(null);

    //  
    useEffect(() => {
        axios
            .get("http://localhost:8000/auth/me", { withCredentials: true })
            .then((res) => setUser(res.data))
            .catch(() => setUser(null));
    }, []);

    // Telegram 
    useEffect(() => {
        window.onTelegramAuth = async (userData) => {
            try {
                await axios.post("http://localhost:8000/auth/login", userData, {
                    withCredentials: true,
                });
                const res = await axios.get("http://localhost:8000/auth/me", {
                    withCredentials: true,
                });
                setUser(res.data);
            } catch (err) {
                console.error("  Telegram :", err);
            }
        };
    }, []);

    const handleLogout = async () => {
        try {
            await axios.post("http://localhost:8000/auth/logout", {}, { withCredentials: true });
            setUser(null);
        } catch (err) {
            console.error("  :", err);
        }
    };

    return (
        <div className="home-container">
            <nav className="top-tabs">
                <span className="tab" onClick={() => navigate("/generate-character")}>
                     
                </span>
                <span className="tab" onClick={() => navigate("/my-projects")}>
                     
                </span>
            </nav>

            <main className="main-content">
                <h1 className="headline">  </h1>
                <p className="subtitle">
                           3 .
                </p>

                <div className="button-group">
                    <button className="btn" onClick={() => navigate("/create-choice")}>
                        
                    </button>

                    {!user ? (
                        <div id="telegram-login" className="telegram-login-wrapper">
                            <script
                                async
                                src="https://telegram.org/js/telegram-widget.js?7"
                                data-telegram-login="zavod_worker_bot" //       @
                                data-size="large"
                                data-userpic="true"
                                data-request-access="write"
                                data-on-auth="onTelegramAuth"
                            ></script>
                        </div>
                    ) : (
                        <div className="user-info">
                            {user.photo_url && (
                                <img src={user.photo_url} alt="avatar" className="user-avatar" />
                            )}
                            <span className="user-name">{user.first_name}</span>
                            <button className="btn logout-btn" onClick={handleLogout}>
                                
                            </button>
                        </div>
                    )}
                </div>
            </main>

            <section className="portfolio">
                {works.map((work) => (
                    <div key={work.id} className="work-item">
                        <img src={work.image} alt={`Work ${work.id}`} />
                        <div className="work-overlay">
                            <p>{work.description}</p>
                        </div>
                    </div>
                ))}
            </section>
        </div>
    );
};

export default Home;
